import React from 'react'
import moment from 'moment'
import { useParams } from 'react-router-dom'
import { getIsObserver, getRole } from './modules/storageHelper'

function EventsViewDesktop(props) {
  const { type } = useParams()
  const isObserver = getIsObserver()
  const role = getRole()

  let title
  switch (type) {
    case 'setda':
      title = ' Gedung & Ruang Rapat'; break;
    case 'adipura':
      title = ' Gedung Adipura'; break;
    case 'bupati':
      title = ' Bupati'; break;
    case 'wakil':
      title = ' Wakil Bupati'; break;
    case 'sekda':
      title = ' Sekretaris Daerah'; break;
    default:
      title = ' Kegiatan'; break;
  }

  return (
    <div className="box">
      <div className="title">Jadwal {title}</div>

      <table className="table is-striped is-fullwidth is-hoverable">
        <thead>
          <tr>
            <th>Hari/Tgl</th>
            <th>Jam</th>
            <th>Tempat</th>
            <th>Kegiatan</th>
            <th>Unit Kerja</th>
            <th>Catatan</th>
          </tr>
        </thead>

        <tbody>
          {props.events.map(event => {
            const dateTime = moment(event.dateTime).local()
            const isMatch = props.match(event.name)
              || props.match(event.place)
              || props.match(event.pic)

            return (
              <tr
                key={event._id}
                style={{ cursor: 'pointer' }}
                className={!isMatch ? 'is-hidden' : ''}
                onClick={() => {
                  if (role === 'super' || role === type)
                    props.showModal(event)
                }}
              >
                <td>
                  {dateTime.format('dddd')}
                  <br />
                  {dateTime.format('D MMM')}
                  <br />
                  {dateTime.format('YYYY')}
                </td>
                <td>{dateTime.format('HH:mm')}</td>
                <td>{event.place}</td>
                <td><b>{event.name}</b></td>
                <td>
                  {event.pic}
                  {event.participant ? ` (${event.participant})` : null}
                </td>
                <td>
                  {event.admin ? <span>Admin: {event.admin}<br /></span> : null}
                  {isObserver ? event.notes : null}
                </td>
              </tr>
            )
          })}
        </tbody>
      </table>
    </div>
  )
}

export default EventsViewDesktop