import React from 'react'

function Notification(props) {
  return (
    <div style={{
      position: "fixed",
      bottom: "0",
      width: "100%",
      zIndex: "1000",
      padding: "1rem"
    }}>
      <div className="notification is-warning"
        style={{
          margin: "0 auto",
          maxWidth: "500px",
        }}
      >
        <button type="button"
          className="delete"
          onClick={props.dismiss}
        />
        {props.children}
      </div>
    </div>
  )
}

export default Notification