import React, { useState, useEffect } from 'react'
import { HashRouter, Switch, Route } from 'react-router-dom'
import { Navbar, Sidebar, Notification, Footer } from './Components'

// route imports
import Home from './Home'
import About from './About'
import NotFound from './NotFound'
import Login from './Login'
import Places from './Places'
import Users from './Users'
import Events from './Events'
import { getName, getUsername } from './modules/storageHelper'

function App() {
  const [name, setName] = useState(getName() || getUsername())
  const [notificationText, setNotificationText] = useState(null)
  const [dimension, setDimension] = useState({
    width: window.innerWidth,
    height: window.innerHeight,
  })

  function notify(text) {
    setNotificationText(text)
    setTimeout(() => {
      setNotificationText(null)
    }, 5000);
  }

  function updateDimension() {
    setDimension({
      width: window.innerWidth,
      height: window.innerHeight,
    })
  }

  useEffect(() => {
    window.addEventListener('resize', updateDimension)
    return () => window.removeEventListener('resize', updateDimension)
  }, [])

  return (
    <HashRouter>
      <Navbar name={name} setName={setName} notify={notify} />

      <div className="container">
        {notificationText ?
          <Notification dismiss={() => setNotificationText(null)}>
            {notificationText}
          </Notification>
          : null
        }
        <div className="columns">

          <Sidebar />

          <div className="column">
            <Switch>
              <Route exact path="/" component={Home} />
              <Route path="/places" component={Places} />
              <Route path="/about" component={About} />

              <Route path="/events/:type" render={props =>
                <Events {...props} notify={notify} dimension={dimension} />
              } />

              <Route path="/login" render={props =>
                <Login {...props} notify={notify} setName={setName} />
              } />

              <Route path="/users" component={Users} />

              {/* handle not found page */}
              <Route component={NotFound} />
            </Switch>

            <article className="message is-small is-warning" style={{ marginTop: "1rem" }}>
              <div className="message-body">
                Aplikasi dalam tahap uji coba. Laporkan ke developer jika terjadi masalah.
              </div>
            </article>

          </div>
        </div>

      </div>

      <Footer />
    </HashRouter>
  )
}

export default App;